.App{
  background-color: rgb(238, 235, 222);
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}




.results > .result:nth-child(5) ~ * { display: none; }

.popup{
  position: fixed;
  top:0;
  left:0;
  width:100%;
height: 100% ;
background-color: rgb(238, 235, 222);
overflow-y:scroll;
}


.container{
display:flex;
flex-direction: column;
align-items: center;

}



.searchbox{
  width:200px;
}

.searchBtn{
  width:200px;
  margin-top:1vh;
  
  border-radius: 30px 30px 30px 30px;
  -webkit-box-shadow: 3px 4px 5px 0px rgba(0,0,0,0.75);
  -moz-box-shadow: 3px 4px 5px 0px rgba(0,0,0,0.75);
  box-shadow: 3px 4px 5px 0px rgba(0,0,0,0.75);
}
.results{
  margin-top:3vh;
  display:grid;
  grid-template-columns: 1fr  1fr;
  grid-gap: 5vw;
  text-align:center;
  cursor: pointer;
}

.result{
  background-color:white;

}
.content{
  background-color:white;
  border: 1px solid #000000;
  margin-left:30px;
  margin-right:30px;
  display:flex;
flex-direction: column;
align-items: center;
}

.posterImg {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 30%;
  height:auto;
}
.plot{
  margin-left:30px;
  margin-right:30px;
}
@media (max-width: 1000px) {
  .results {
    grid-template-columns: 1fr;
  }
}